// Serviciu pentru operaiuni CRUD i pagination pe tranzacii folosind Supabase
// OWNER: echipa API & FE
// Orice modificare la acest serviciu trebuie documentat n DEV_LOG.md

import { supabase } from './supabase';
import { TransactionValidated, CreateTransaction } from '@shared-constants/transaction.schema';
import { CATEGORIES } from '@shared-constants/categories';
import { MESAJE } from '@shared-constants/messages';

export interface Pagination {
  limit?: number;
  offset?: number;
  sort?: 'date' | 'amount' | 'created_at';
  order?: 'asc' | 'desc';
}

export interface TransactionPage {
  data: TransactionValidated[];
  count: number;
}

const TABLE = 'transactions';

export const supabaseService = {
  // Fetch tranzacii cu paginare i filtrare opional
  async fetchTransactions(
    userId: string | undefined, // Am actualizat tipul pentru a permite undefined
    pagination: Pagination = {},
    filters: Partial<Pick<TransactionValidated, 'type' | 'category' | 'recurring'>> & {
      dateFrom?: string; // Data de nceput pentru interval (format ISO: YYYY-MM-DD)
      dateTo?: string;   // Data de sfrit pentru interval (format ISO: YYYY-MM-DD)
    } = {}
  ): Promise<TransactionPage> {
    let query = supabase
      .from(TABLE)
      .select('*', { count: 'exact' });

    // Filtrare dup user_id doar dac exist userId (pentru mod demo, fr login, nu filtrm)
    if (userId) {
      query = query.eq('user_id', userId);
    }

    if (filters.type) query = query.eq('type', filters.type);
    if (filters.category) query = query.eq('category', filters.category);
    if (filters.recurring !== undefined) query = query.eq('recurring', filters.recurring);
    
    // Filtrare dup interval de date
    if (filters.dateFrom) query = query.gte('date', filters.dateFrom);
    if (filters.dateTo) query = query.lte('date', filters.dateTo);

    if (pagination.sort) query = query.order(pagination.sort, { ascending: pagination.order === 'asc' });
    if (pagination.limit !== undefined && pagination.offset !== undefined) {
      query = query.range(pagination.offset, pagination.offset + pagination.limit - 1);
    }

    const { data, count, error } = await query;
    if (error) throw error;
    return { data: data as TransactionValidated[], count: count || 0 };
  },

  // Helper pentru validarea categoriei i subcategoriei - MODIFICAT pentru a accepta subcategorii personalizate
  validateCategoryAndSubcategory(category: string | undefined, subcategory: string | null | undefined): boolean {
    // Logging pentru debugging mbuntit
    console.debug(`[validateCategoryAndSubcategory] Validare: category=${category}, subcategory=${subcategory}`);
    
    // 0. Verificm dac categoria este definit
    if (!category) {
      console.error(`Categoria este obligatorie!`);
      return false;
    }
    
    // 1. Verificm dac categoria exist n CATEGORIES
    if (!Object.keys(CATEGORIES).includes(category)) {
      console.warn(`Categorie "${category}" nu exist n lista predefinit. Poate fi o categorie personalizat.`);
      // Nu mai returnam false, permitem categorii personalizate
    }
    
    // 2. Acceptm toate subcategoriile cu logging explicit pentru debugging
    if (subcategory) {
      console.debug(`[DEBUG] Subcategorie "${subcategory}" acceptat pentru categoria "${category}" (validare delegat backend-ului).`);
    }
    
    // Delegm validarea complet ctre backend
    return true;
  },

  // Creeaz tranzacie nou
  async createTransaction(payload: CreateTransaction): Promise<TransactionValidated> {
    // Import store-ul aici pentru a evita circularitatea la importuri
    const { useAuthStore } = await import('../stores/authStore');
    const user = useAuthStore.getState().user;
    if (!user || !user.id) throw new Error(MESAJE.EROARE_NECUNOSCUTA || 'Utilizatorul nu este autentificat!');
    
    // Validm categoria i subcategoria - DEZACTIVAT, doar logm pentru debugging
    // Am pstrat apelul pentru compatibilitate cu codul existent
    this.validateCategoryAndSubcategory(payload.category, payload.subcategory || null);
    
    const payloadWithUser = { ...payload, user_id: user.id };
    const { data, error } = await supabase
      .from(TABLE)
      .insert([payloadWithUser])
      .select()
      .single();
      
    if (error) {
      console.error('Error creating transaction:', error);
      
      // Folosim mesajele din constante pentru erori cu mesaj mbuntit
      if (error.message?.includes('category') || error.message?.includes('subcategory')) {
        // Folosim mesajul standard din MESAJE pentru a menine consistena UI
        const errorMessage = MESAJE.EROARE_CATEGORIE_SUBCATEGORIE_INVALIDA || 
          'Categoria sau subcategoria nu este valid. Verificai categoriile disponibile n aplicaie.';
        throw new Error(errorMessage);
      }
      
      throw error;
    }
    
    return data as TransactionValidated;
  },

  // Update tranzacie existent
  async updateTransaction(id: string, payload: Partial<CreateTransaction>): Promise<TransactionValidated> {
    const { useAuthStore } = await import('../stores/authStore');
    const user = useAuthStore.getState().user;
    if (!user || !user.id) throw new Error(MESAJE.EROARE_NECUNOSCUTA || 'Utilizatorul nu este autentificat!');
    
    // Validm categoria i subcategoria dac sunt furnizate - DEZACTIVAT, doar logm pentru debugging
    // Am pstrat apelurile pentru compatibilitate cu codul existent
    if (payload.category !== undefined) {
      this.validateCategoryAndSubcategory(payload.category, payload.subcategory || null);
    }
    
    const { data, error } = await supabase
      .from(TABLE)
      .update(payload)
      .eq('id', id)
      .eq('user_id', user.id)
      .select()
      .single();
      
    if (error) {
      console.error('Error updating transaction:', error);
      
      // Folosim mesajele din constante pentru erori cu mesaj mbuntit
      if (error.message?.includes('category') || error.message?.includes('subcategory')) {
        // Folosim mesajul standard din MESAJE pentru a menine consistena UI
        const errorMessage = MESAJE.EROARE_CATEGORIE_SUBCATEGORIE_INVALIDA || 
          'Categoria sau subcategoria nu este valid. Verificai categoriile disponibile n aplicaie.';
        throw new Error(errorMessage);
      }
      
      throw error;
    }
    
    return data as TransactionValidated;
  },

  // terge tranzacie
  async deleteTransaction(id: string): Promise<void> {
    const { useAuthStore } = await import('../stores/authStore');
    const user = useAuthStore.getState().user;
    if (!user || !user.id) throw new Error(MESAJE.EROARE_NECUNOSCUTA || 'Utilizatorul nu este autentificat!');
    const { error } = await supabase
      .from(TABLE)
      .delete()
      .eq('id', id)
      .eq('user_id', user.id);
    if (error) throw error;
  }
};