import React from 'react';
import { EXCEL_GRID, UI } from '@shared-constants/ui';
import LunarGrid from '../components/features/LunarGrid';
import LunarGridTanStack from '../components/features/LunarGrid/LunarGridTanStack';
import { TITLES } from '@shared-constants';
import { CATEGORIES } from '@shared-constants/categories';
import { useTransactionStore } from '../stores/transactionStore';
import { useCategoryStore } from '../stores/categoryStore';
import { useAuthStore } from '../stores/authStore';
import { useQueryClient } from '@tanstack/react-query';
import { useMonthlyTransactions } from '../services/hooks/useMonthlyTransactions';

/**
 * Pagin dedicat pentru afiarea grid-ului lunar
 * Permite navigarea ntre luni i vizualizarea tranzaciilor pe zile/categorii
 * Cu debounce implementat pentru a evita prea multe cereri API la navigare rapid
 */
const LunarGridPage: React.FC = () => {
  // Acces la queryClient pentru a gestiona invalidarea cache-ului n mod eficient
  const queryClient = useQueryClient();
  
  // State pentru anul i luna curent
  const [year, setYear] = React.useState(() => new Date().getFullYear());
  const [month, setMonth] = React.useState(() => new Date().getMonth() + 1);
  
  // Timer pentru debounce - evitm cereri API multiple cnd utilizatorul schimb rapid luna/anul
  const debounceTimerRef = React.useRef<number | null>(null);
  
  // State pentru a alege ntre implementarea clasic i cea TanStack
  const [useTanStack, setUseTanStack] = React.useState(() => {
    // ncercam s citim preferina din localStorage, default la true pentru versiunea nou
    const savedPreference = localStorage.getItem('lunarGrid-useTanStack');
    return savedPreference !== null ? savedPreference === 'true' : true;
  });
  
  // Extragem user din AuthStore
  const { user } = useAuthStore();
  
  // Folosim React Query prin hook-ul useMonthlyTransactions pentru verificarea strii de loading
  // Aceasta nlocuiete vechile referine la useTransactionStore.loading i fetchTransactions
  const { isLoading: loading } = useMonthlyTransactions(
    year, 
    month, 
    user?.id,
    { includeAdjacentDays: true }
  );
  
  // Funcionalitate pentru categorii personalizate
  const loadCategories = useCategoryStore(state => state.loadUserCategories);
  const mergeWithDefaults = useCategoryStore(state => state.mergeWithDefaults);
  
  // Iniializm store-ul de categorii la prima ncrcare
  // Acest efect respect regulile globale prin:
  // 1. Operare o singur dat la montare ([] ca dependene)
  // 2. Folosirea useRef pentru a preveni efecte multiple
  const categoriesLoadedRef = React.useRef(false);
  
  // Efect pentru ncrcarea categoriilor personalizate i fuzionarea cu cele predefinite
  React.useEffect(() => {
    // Guard pentru a preveni ncrcri multiple
    if (categoriesLoadedRef.current) return;
    
    // Doar pentru utilizatorii autentificai
    if (!user) return;
    
    const initializeCategories = async () => {
      try {
        console.log('LunarGridPage: Iniializare categorii personalizate');
        
        // 1. Mai nti ncrcm categoriile personalizate din DB
        await loadCategories(user.id);
        
        // 2. Apoi fuzionm cu cele predefinite din CATEGORIES (shared-constants)
        // Conversia e necesar pentru c CATEGORIES are un format uor diferit de CustomCategory[]
        const defaultCategories = Object.entries(CATEGORIES).map(([name, subcats]) => ({
          name,
          subcategories: Object.values(subcats).flat().map(subcatName => ({
            name: subcatName,
            isCustom: false
          })),
          isCustom: false
        }));
        
        // 3. Fuziune - prioritate pentru cele personalizate
        mergeWithDefaults(defaultCategories);
        
        // Marcm ca ncrcat pentru a preveni operaii duplicate
        categoriesLoadedRef.current = true;
        
        console.log('LunarGridPage: Categorii iniializate cu succes');
      } catch (error) {
        console.error('Eroare la iniializarea categoriilor:', error);
      }
    };
    
    initializeCategories();
  }, [user, loadCategories, mergeWithDefaults]); // Dependene minimale necesare
  
  // Actualizm URL-ul cnd se schimb luna/anul
  // Nu mai avem nevoie de logica de fetch, este gestionat de React Query
  React.useEffect(() => {
    // Actualizm URL-ul cu parametrul month=YYYY-MM i pstrm hash
    const urlParams = new URLSearchParams(window.location.search);
    urlParams.set('month', `${year}-${month.toString().padStart(2, '0')}`);
    const hash = window.location.hash;
    const newUrl = `${window.location.pathname}?${urlParams}${hash}`;
    window.history.replaceState({}, '', newUrl);
    
    console.log(`LunarGridPage: URL updated for ${year}-${month}`);
  }, [year, month]);
  
  // Nu mai avem nevoie de efectul pentru fetch iniial
  // React Query se ocup automat de aceasta n useTransactions
  // Urmtorul comentariu este doar pentru documentare
  /* 
   * React Query gestioneaz automat starea pentru:
   * - Fetch iniial (la montare)
   * - Refetch la schimbarea dependenelor (year, month)
   * - Cache i invalidare cache
   * - Loading states & error handling
   */
  
  /**
   * Funcie de debounce care actualizeaz luna/anul i invalideaz cache-ul cu ntrziere
   * Evit multiple cereri API cnd utilizatorul navigheaz rapid ntre luni
   */
  const setDateWithDebounce = React.useCallback((newMonth: number, newYear: number) => {
    // Actualizm imediat UI pentru a oferi feedback utilizatorului
    setMonth(newMonth);
    setYear(newYear);
    
    // Anulm orice timer de debounce n curs
    if (debounceTimerRef.current !== null) {
      window.clearTimeout(debounceTimerRef.current);
    }
    
    // Setm un nou timer pentru invalidarea cache-ului dup 300ms 
    // de la ultima aciune a utilizatorului
    debounceTimerRef.current = window.setTimeout(() => {
      console.log(`Invalidating cache for ${newYear}-${newMonth} after debounce`);
      
      // Folosim queryClient pentru a invalida doar query-ul specific pentru luna/anul selectat
      // Aceast abordare este mai eficient dect invalidarea tuturor query-urilor
      queryClient.invalidateQueries({ 
        queryKey: ['transactions', newYear, newMonth],
        exact: true 
      });
      
      debounceTimerRef.current = null;
    }, 300); // 300ms ntrziere - valoare optim pentru UX + performan
  }, [queryClient]);
  
  // Funcii pentru navigare ntre luni, acum cu debounce
  const goToPreviousMonth = () => {
    let newMonth = month - 1;
    let newYear = year;
    
    if (newMonth < 1) {
      newMonth = 12;
      newYear--;
    }
    
    setDateWithDebounce(newMonth, newYear);
  };
  
  const goToNextMonth = () => {
    let newMonth = month + 1;
    let newYear = year;
    
    if (newMonth > 12) {
      newMonth = 1;
      newYear++;
    }
    
    setDateWithDebounce(newMonth, newYear);
  };
  
  // Formatare nume lun n romn
  const getMonthName = (month: number) => {
    const monthNames = [
      'Ianuarie', 'Februarie', 'Martie', 'Aprilie', 'Mai', 'Iunie',
      'Iulie', 'August', 'Septembrie', 'Octombrie', 'Noiembrie', 'Decembrie'
    ];
    return monthNames[month - 1];
  };
  
  // Salvm preferina utilizatorului n localStorage pentru a o reine ntre sesiuni
  React.useEffect(() => {
    localStorage.setItem('lunarGrid-useTanStack', useTanStack.toString());
  }, [useTanStack]);

  // Toggle ntre implementarea clasic i implementarea TanStack
  const toggleImplementation = () => {
    setUseTanStack(prev => !prev);
  };

  return (
    <>
      <div className="flex justify-between items-center mb-token">
        <h1 className="text-2xl font-bold text-primary-700" data-testid="lunar-grid-title">
          {TITLES.GRID_LUNAR}
        </h1>
        
        <div className="flex items-center space-x-token-sm">
          {/* Toggle pentru implementare */}
          <div 
            className="flex items-center bg-secondary-100 rounded-lg p-1 shadow-sm" 
            data-testid="implementation-toggle"
          >
            <button 
              className={`px-3 py-1 text-sm rounded-md transition-all ${useTanStack ? 'bg-primary-500 text-white' : 'text-primary-600'}`}
              onClick={toggleImplementation}
              data-testid="tanstack-toggle-btn"
            >
              {EXCEL_GRID.TABLE_CONTROLS.VIRTUAL_TABLE}
            </button>
            <button 
              className={`px-3 py-1 text-sm rounded-md transition-all ${!useTanStack ? 'bg-primary-500 text-white' : 'text-primary-600'}`}
              onClick={toggleImplementation}
              data-testid="legacy-toggle-btn"
            >
              {EXCEL_GRID.TABLE_CONTROLS.LEGACY_TABLE}
            </button>
          </div>

          {/* Link to Options for managing categories */}
          <div>
            <a
              href="#options"
              className="text-sm text-accent-600 hover:text-accent-700 underline"
              data-testid="manage-categories-link"
              onClick={e => {
                e.preventDefault();
                const newUrl = `${window.location.pathname}#options`;
                window.history.replaceState({}, '', newUrl);
                window.dispatchEvent(new HashChangeEvent('hashchange'));
              }}
            >
              {UI.MANAGE_CATEGORIES}
            </a>
          </div>
        </div>
        
        <div className="flex items-center gap-token">
          <button 
            onClick={goToPreviousMonth}
            className="btn-icon bg-secondary-200 text-secondary-700 hover:bg-secondary-300 focus:outline-none focus:ring-2 focus:ring-primary-500"
            data-testid="prev-month-btn"
          >
            &larr;
          </button>
          
          <span className="text-lg font-medium text-primary-700" data-testid="current-month">
            {getMonthName(month)} {year}
          </span>
          
          <button 
            onClick={goToNextMonth}
            className="btn-icon bg-secondary-200 text-secondary-700 hover:bg-secondary-300 focus:outline-none focus:ring-2 focus:ring-primary-500"
            data-testid="next-month-btn"
          >
            &rarr;
          </button>
        </div>
      </div>
      
      {/* Afim o versiune diferit n funcie de preferina utilizatorului */}
      {loading ? (
        <div className="text-center py-token-xl text-secondary-600" data-testid="loading-indicator">{EXCEL_GRID.LOADING}</div>
      ) : useTanStack ? (
        <div data-testid="tanstack-implementation">
          <LunarGridTanStack year={year} month={month} />
          <div className="mt-token text-xs text-secondary-500 text-right">
            {EXCEL_GRID.TABLE_CONTROLS.VERSION}: TanStack (optimizat pentru performan)
          </div>
        </div>
      ) : (
        <div data-testid="legacy-implementation">
          <LunarGrid year={year} month={month} />
          <div className="mt-token text-xs text-secondary-500 text-right">
            {EXCEL_GRID.TABLE_CONTROLS.VERSION}: Clasic
          </div>
        </div>
      )}
    </>
  );
};

export default LunarGridPage;
